import { Avatar } from "primereact/avatar";
import { Card } from "primereact/card";
import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { ProjectApi } from "../../../api";
import { routes } from "../../../routes";
import { setDateString } from "../../../utils/dates";
import { getURLImage } from "../../../utils/images";

const CurrentProject = (props) => {
  const user = useSelector((state) => state.user.value);
  const freelancerId = user.id;
  const [projects, setProjects] = useState([]);
  // const navigate = useNavigate();

  useEffect(() => {
    const getProjects = async () => {
      const servs = await ProjectApi.getProjectsByFreelancerId(freelancerId);
      setProjects(servs);
    };
    if (freelancerId) {
      getProjects();
    }
  }, [freelancerId]);
  return (
    <>
      {/* <SectionContent type="light"> */}
      <div className="pb-6 mt-5">
        <div className="has-text-centered mb-4 is-hidden-widescreen">
          <p className="p-18-dark">
            <b>Proyectos en proceso</b>
          </p>
        </div>
        <div>
          {projects.map((p, i) => (
            <div className="mt-1" key={i}>
              <Card>
                {/* <p className="m-0 p-3">
                    Lorem ipsum dolor sit amet, consectetur adipisicing elit.
                    Inventore sed consequuntur error repudiandae numquam
                    deserunt quisquam repellat libero asperiores earum nam
                    nobis, culpa ratione quam perferendis esse, cupiditate neque
                    quas!
                  </p> */}
                <div className="columns is-vcentered">
                  <div className="column is-1">
                    <img
                      alt="Ver proyecto"
                      src={getURLImage(p.service?.category?.image)}
                    />
                  </div>
                  <div className="column is-3">
                    <b>{p.service?.category?.name}</b>
                  </div>
                  <div className="column is-2">
                    {setDateString(p.creationDate)}
                  </div>
                  <div className="column is-2">
                    {setDateString(p.calculatedFinishDate)}
                  </div>
                  <div className="column is-2">
                    <p className="p-blue-weigth">${p.package?.cost} MXN</p>
                  </div>
                  <div className="column has-text-right">
                    {p.freelancer?.photo && (
                      <Avatar
                        className="ml-3 mt-2"
                        image={getURLImage(p.user?.photo)}
                        size="large"
                        shape="circle"
                      />
                    )}
                  </div>
                  <div className="column has-text-right">
                    <Link
                      to={
                        "/" +
                        routes.DASHBOARD_FREELANCER_PROJECTS_DETAIL.replace(
                          ":id",
                          p.id
                        )
                      }
                    >
                      <img
                        width={23}
                        alt="Ver proyecto"
                        src={getURLImage("images/eye.png", true)}
                        style={{
                          minWidth: "23px",
                        }}
                      />
                    </Link>
                  </div>
                </div>
              </Card>
              <br />
            </div>
          ))}
        </div>
      </div>
      {/* <Toast ref={toast}></Toast> */}
      {/* </SectionContent> */}
    </>
  );
};

export default CurrentProject;
